import React from "react";
import Draggable from "react-draggable";
import "./App.css";

export default function Tile({ letter, tileNumber, onClick, topMostTile }) {
  
    const handleClick = () => {
      onClick(tileNumber)
  };

  const isTopMost = topMostTile === tileNumber ? 'shell' : ''
 
 

  return (
    <Draggable bounds="parent">
      <div className={isTopMost} onMouseOver={() => handleClick()}>
        <div className="tile">
          <p>{letter}</p>
        </div>
      </div>
    </Draggable>
  );
}
