import React, { Component } from 'react'
import Tile from './Tile'
import LetterBar from './LetterBar'

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      letters: "",
      topMostTile: 0
    };
  }

  handleInput = (value) => {
    this.setState({
      letters: value
    });
  };

  handleOnClick = (value) => {
    this.setState({
      topMostTile: value
    })
  }

  render() {
    let chunk = this.state.letters.replace(/\s/g, "");
    let arr = chunk.split("");
    let arr1 = arr.map((l, i) => <Tile letter={l} tileNumber={i} key={l + i} onClick={this.handleOnClick} topMostTile={this.state.topMostTile}/>);

    return (
      <div className="app">
        <LetterBar onInputChange={this.handleInput} />
        <div className="container">
          {arr1}
        </div>
      </div>
    );
  }
}

